@import url(https://fonts.googleapis.com/css2?family=Noto+Serif+KR:wght@400;700;900);
body {
  margin: 0;
  font-family: "Noto Serif KR", -apple-system, BlinkMacSystemFont;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body,
#root {
  height: 100%;
}

.App {
  text-align: left;
  height: 100%;
}

.App > div {
	height: 100%;
}

.main {
