}

#toast {
  position: fixed;
  bottom: 30px;
  left: 50%;
  padding: 15px 20px;
  transform: translate(-50%, 10px);
  border-radius: 30px;
  overflow: hidden;
  font-size: .8rem;
  opacity: 0;
  visibility: hidden;
  transition: opacity .5s, visibility .5s, transform .5s;
  background: rgba(0, 0, 0, .35);
  color: #fff;
  z-index: 10000;
}

#toast.reveal {
  opacity: 1;
  visibility: visible;
  transform: translate(-50%, 0)
}

.kakao-share-button > button {
  border: 0;
  background: none;
  box-shadow: none;
  border-radius: 0px;
}
.gallery-detail {
  z-index: 999;
  position: fixed;
  overflow: hidden;
  overflow-y:auto;
  -webkit-overflow-scrolling: touch;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #FFFFFF;
  max-width: 1024px;
  -webkit-animation: moveUp 0.5s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
          animation: moveUp 0.5s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
}

.gallery-detail.out {
  -webkit-animation: moveDown 0.5s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
          animation: moveDown 0.5s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
}

@-webkit-keyframes moveUp {
  /* 하단에서 */ 0% { transform: translateY(100vh); } /* 중앙으로 위치 */ 100% { transform: translateY(0); }
}

@keyframes moveUp {
  /* 하단에서 */ 0% { transform: translateY(100vh); } /* 중앙으로 위치 */ 100% { transform: translateY(0); }
}

@-webkit-keyframes moveDown {
  /* 하단에서 */ 0% { transform: translateY(0); } /* 중앙으로 위치 */ 100% { transform: translateY(100vh); }
}

@keyframes moveDown {
  /* 하단에서 */ 0% { transform: translateY(0); } /* 중앙으로 위치 */ 100% { transform: translateY(100vh); }
}

